.main-nav__toggle {
  position: absolute;
  top: -45px;
  right: 18px;
  z-index: 2;
  display: block;
  width: 50px;
  height: 24px;
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
}
